import Endpoint from '@/services/Endpoint'

export default {
  loanStatuses (payload) {
    return Endpoint().get('/s1/loan/loanStatus', {
      params: payload
    })
  },
  createLoanStatus (payload) {
    return Endpoint().post('/s1/loan/loanStatus', payload)
  },
  modifyLoanStatus (payload) {
    return Endpoint().patch('/s1/loan/loanStatus', payload)
  }
}
