<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('STATUS_NAME') }}</label>
          <vs-input class="w-full" v-model="loanStatus.statusName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('STATUS_BEHAVIOUR') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="loanStatus.statusBehaviourEnumId">
            <vs-select-item v-for="(enumeration, index) in statusBehaviours" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
          <vs-input class="w-full" v-model="loanStatus.externalStatusId"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyLoanStatus('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyLoanStatus('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyLoanStatus('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoanStatusService from '@/services/loan/LoanStatusService'
import EnumerationService from '@/services/commons/EnumerationService'

export default {
  name: 'loan-status-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      loanStatus: {},
      statusBehaviours: []
    }
  },
  methods: {
    async load () {
      let self = this
      LoanStatusService.loanStatuses({
        statusId: this.$route.params.id
      }).then(function (response) {
        self.loanStatus = response.data.loanStatusList[0] || {}
        self.loanStatus.disabled = self.loanStatus.disabled === 'Y'
      })

      EnumerationService.enumerations({
        enumTypeId: 'LoanStatusBehaviour',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.statusBehaviours = response.data.enumerationList || []
      })
    },
    modifyLoanStatus (nextAction) {
      let self = this
      LoanStatusService.modifyLoanStatus({
        statusId: this.$route.params.id,
        statusName: this.loanStatus.statusName,
        statusBehaviourEnumId: this.loanStatus.statusBehaviourEnumId,
        externalStatusId: this.loanStatus.externalStatusId
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewLoanStatus', params: { id: response.data.statusId } })
          } else {
            self.$router.push({ name: 'BrowseLoanStatuses' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
